<template>
	<div>
	<contentHeader title="Data Obat" />
	<div class="content-header">
		<div class="container-fluid">
			<div class="row justify-content-center">
				<div class="col">
					<div class="card">
						<div class="card-header row d-flex align-items-center">
	            			<h3 class="card-title col-6">Table Obat</h3>
	            			<div class="col-6 text-right">
	            				<router-link to="/tambah-data-obat"><button class="btn btn-sm btn-info">Tambah</button></router-link>	
	            			</div>
	            		</div>
	            		<div class="card-body">
	            			<table class="table table-bordered table-hover">
							  <thead>
							    <tr>
							      <th scope="col" class="text-sm">NO</th>	
							      <th scope="col" class="text-sm">KODE</th>
							      <th scope="col" class="text-sm">NAMA</th>
							      <th scope="col" class="text-sm">JUMLAH STOK</th>
							      <th scope="col" class="text-sm">SATUAN</th>
							      <th scope="col" class="text-sm">HARGA JUAL</th>
							      <th scope="col" class="text-sm">ACTION</th>
							    </tr>
							  </thead>
							  <tbody>
							    <tr v-for="(row, index) in obat" :key="row.id">
							      <td class="text-sm" scope="row">{{ index+1 }}</td>
							      <td class="text-sm" scope="row">{{ row.kode }}</td>
							      <td class="text-sm" scope="row">{{ row.nama }}</td>
							      <td class="text-sm" scope="row">{{ row.stok_akhir }}</td>
							      <td class="text-sm" scope="row">{{ row.satuan }}</td>
							      <td class="text-sm" scope="row">{{ row.harga_jual }}</td>
							      <td class="text-sm">
							      	<div class="btn-group">
					                    <button type="button" class="btn btn-sm btn-info text-sm">Proses</button>
					                    <button type="button" class="btn btn-sm btn-info dropdown-toggle dropdown-icon" data-toggle="dropdown" aria-expanded="false">
					                      <span class="sr-only"></span>
					                    </button>
					                    <div class="dropdown-menu" role="menu" style="">
					                      <router-link :to="`/edit-data-obat/${row.id}`" class="dropdown-item text-sm">Edit</router-link>
					                      <div class="dropdown-divider"></div>
					                      <button @click="konfirmasiHapus(row.id)" class="dropdown-item text-danger">Hapus</button>
					                    </div>
					                  </div>
							      </td>
							    </tr>
							  </tbody>
							</table>
							<p class="text-center mt-4">{{ cekData }}</p>
	            		</div>
					</div>
				</div>
			</div>
		</div>
	</div>
		
	</div>
</template>

<script>
	import ContentHeader from '@/components/ContentHeader'

	import { computed, ref, onMounted } from 'vue'
	import store from '@/store'
	import axios from 'axios'
	import Swal from 'sweetalert2'

	export default{
		components: {
			ContentHeader: ContentHeader
		},
		setup(){
			const user = computed(() => store.getters['auth/user'])
			const obat = ref([])
			const cekData = ref('')
			const getObat = async () => {
				let { data } = await axios.get(`api/obat/get/${user.value.cabang_id}`)

				if (data == 'kosong') {
					cekData.value = 'Data Masih Kosong'
					obat.value = null
				} else {
					cekData.value = ''
					obat.value = data
				}
			} 

			const konfirmasiHapus = (id) => {
				Swal.fire({
				  title: 'Hapus Data?',
				  icon: 'warning',
				  showCancelButton: true,
				  confirmButtonText: `Ya`,
				  cancelButtonText: `Batal`,
				}).then((result) => {
				  /* Read more about isConfirmed, isDenied below */
				  if (result.isConfirmed) {
				  	hapusDataObat(id)
				  } 
				})
			}

			const hapusDataObat = (id) => {
	    		axios.delete(`api/obat/destroy/${id}`)
	    		.then((response) => {
	    			Swal.fire({
					  title: 'Berhasil!',
					  text: 'Berhasil Menghapus Data',
					  icon: 'success',
					  showConfirmButton: false,
					  timer: 1500
					})
	    			console.log(response.data)
	    		})
	    		.catch((error) => {
	    			Swal.fire({
					  title: 'Gagal!',
					  text: 'Gagal menghapus data',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})
	    			console.log(error)
	    		})

	    		getObat()
	    	}

			onMounted(() => {
				getObat()
			})

			return {
				obat, cekData, konfirmasiHapus
			}
		}
	}
</script>